/* Icon */

	.icon {
		@include icon;
		border-bottom: none;
		position: relative;

		> .label {
			display: none;
		}

		&:before {
			line-height: inherit;
		}

		&.solid {
			&:before {
				font-weight: 900;
			}
		}

		&.brands {
			&:before {
				font-family: 'Font Awesome 5 Brands';
			}
		}
	}

	.iconify
	{
		min-width: 1.5em;
		min-height: 1.5em;

		header &
		{
			min-width: 2em;
			min-height: 2em;
		}
	}